body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.game {
  /* Fit the game to the inner content */
  display: inline-block;
  margin: 20px;
  border: ridge 10px darkgray;
  border-top: ridge 10px #fff;
  border-left: ridge 10px #fff;
}

.scoreboard {
  background-color: #bdbdbd;
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 15px;
  font-size: 24px;
  border: ridge 10px darkgray;
  border-top: ridge 10px #fff;
  border-left: ridge 10px #fff;
}

.reset-container {
  border: ridge 4px darkgray;
  border-top: ridge 4px #fff;
  border-left: ridge 4px #fff;
}

.reset-button {
  cursor: pointer;
  background: transparent;
  border: none;
}

.board {
  display: flex;
  flex-direction: column;
}

.row {
  display: flex;
}

.cell {
  height: 30px;
  width: 30px;
  min-width: 30px;
  background-color: #bdbdbd;
  border: ridge 4px darkgray;
  border-top: ridge 4px #fff;
  border-left: ridge 4px #fff;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}

.revealed {
  background-color: #bdbdbd;
  border: solid 1px gray;
}

.exploded {
  background-color: red;
}

.adj-1 {
  color: blue;
}

.adj-2 {
  color: green;
}

.adj-3 {
  color: red;
}

.adj-4 {
  color: purple;
}

.adj-5 {
  color: maroon;
}

.adj-6 {
  color: turquoise;
}

.adj-7 {
  color: black;
}

.adj-8 {
  color: gray;
}

.revealed.exploded {
  background-color: red;
}
